* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

body {
  background: url(../images/2850815.jpg);
  background-size: cover;
  background-position: bottom;
  overflow: hidden;
}

.activeApp {
  display: flex;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
}

.activeHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80px;
  flex-wrap: nowrap;
}

h1.active {
  position: absolute;
  margin: 15px;
  top: 2%;
  left: 6%;
  font-size: 4vh;
  transform: translate(-50%, 0);
  color: rgba(255, 255, 255, 0.4);
}

.activeMain {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  animation: show 2s linear;
  animation-iteration-count: 1;
  opacity: 1;
}

.unActiveApp {
  display: flex;
  background: url(../images/2850815.jpg);
  background-size: cover;
  background-position: bottom;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  overflow: hidden;
}

.unActiveHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 10vh;
  flex-wrap: nowrap;
}

h1.unActive {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 5vw;
  white-space: nowrap;
  color: rgba(255, 255, 255, 0.5);
}

.unActiveMain {
  display: none;
  flex-wrap: wrap;
  width: 100vw;
  height: 100%;
  justify-content: space-around;
  align-items: center;
}

@media (max-width: 641px) {
  .activeApp {
    display: flex;
    height: 100vh;
    width: 100vw;
  }

  .activeHeader {
    display: flex;
    justify-content: space-between;
    height: 80px;
    flex-wrap: nowrap;
  }

  h1.active {
    display: none;
  }

  .activeMain {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    animation: show 2s linear;
    animation-iteration-count: 1;
    opacity: 1;
  }
  h1.unActive {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 5vw;
    white-space: nowrap;
    color: rgba(255, 255, 255, 0.5);
  }

  .unActiveMain {
    display: none;
    flex-wrap: wrap;
    width: 100vw;
    height: 100%;
    justify-content: space-around;
    align-items: center;
  }
}

@media (max-width: 940px) {
  h1.unActive {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 10vw;
    white-space: nowrap;
    color: rgba(255, 255, 255, 0.5);
  }
}

@keyframes uph1 {
  0% {
    top: 20%;
  }

  50% {
    top: 15%;
  }

  100% {
    top: 10%;
  }
}
