@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

body {
  background: url(/weatherApp/static/media/2850815.c04b9438.jpg);
  background-size: cover;
  background-position: bottom;
  overflow: hidden;
}

.activeApp {
  display: flex;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
}

.activeHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80px;
  flex-wrap: nowrap;
}

h1.active {
  position: absolute;
  margin: 15px;
  top: 2%;
  left: 6%;
  font-size: 4vh;
  transform: translate(-50%, 0);
  color: rgba(255, 255, 255, 0.4);
}

.activeMain {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  animation: show 2s linear;
  animation-iteration-count: 1;
  opacity: 1;
}

.unActiveApp {
  display: flex;
  background: url(/weatherApp/static/media/2850815.c04b9438.jpg);
  background-size: cover;
  background-position: bottom;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  overflow: hidden;
}

.unActiveHeader {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 10vh;
  flex-wrap: nowrap;
}

h1.unActive {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 5vw;
  white-space: nowrap;
  color: rgba(255, 255, 255, 0.5);
}

.unActiveMain {
  display: none;
  flex-wrap: wrap;
  width: 100vw;
  height: 100%;
  justify-content: space-around;
  align-items: center;
}

@media (max-width: 641px) {
  .activeApp {
    display: flex;
    height: 100vh;
    width: 100vw;
  }

  .activeHeader {
    display: flex;
    justify-content: space-between;
    height: 80px;
    flex-wrap: nowrap;
  }

  h1.active {
    display: none;
  }

  .activeMain {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    animation: show 2s linear;
    animation-iteration-count: 1;
    opacity: 1;
  }
  h1.unActive {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 5vw;
    white-space: nowrap;
    color: rgba(255, 255, 255, 0.5);
  }

  .unActiveMain {
    display: none;
    flex-wrap: wrap;
    width: 100vw;
    height: 100%;
    justify-content: space-around;
    align-items: center;
  }
}

@media (max-width: 940px) {
  h1.unActive {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 10vw;
    white-space: nowrap;
    color: rgba(255, 255, 255, 0.5);
  }
}

@keyframes uph1 {
  0% {
    top: 20%;
  }

  50% {
    top: 15%;
  }

  100% {
    top: 10%;
  }
}

input.unActive {
  display: flex;
  position: absolute;
  top: 35%;
  width: 50%;
  justify-self: center;
  align-self: center;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  font-size: 28px;
  border-radius: 16px 16px 16px 16px;
}

input.Active {
  display: flex;
  position: absolute;
  top: 0;
  width: 40%;
  height: 6%;
  justify-self: center;
  align-self: center;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  font-size: 28px;
  border-radius: 0 0 16px 16px;
  transition: all 1s ease, all 1s ease;
}

input.Active:focus {
  width: 45%;
  height: 8%;
}

input:focus {
  background-color: rgba(255, 255, 255, 1);
}

p {
  line-height: 35px;
}

@media (max-width: 641px) {
  input.Active {
    display: flex;
    position: absolute;
    top: 5%;
    width: 80%;
    height: 6%;
    justify-self: center;
    align-self: center;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: none;
    border: none;
    outline: none;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.5);
    font-size: 28px;
    border-radius: 16px 16px 16px 16px;
    transition: all 1s ease, all 1s ease;
  }

  input.Active:focus {
    width: 85%;
    height: 6%;
  }

  input:focus {
    background-color: rgba(255, 255, 255, 1);
  }
}

@keyframes move {
  0% {
    top: 35%;
  }

  50% {
    top: 15%;
  }

  100% {
    top: 0%;
  }
}

.locationDate {
  display: flex;
  font-weight: bold;
  width: 40%;
  height: 60%;
  flex-wrap: wrap;
}

.locationDate > span:nth-child(2) {
  font-size: 4vw;
  width: 15vw;
  align-self: flex-end;
  height: 30vh;
}

.locationDate > span:nth-child(3) {
  font-size: 3vh;
  width: 100%;
  text-align: center;
}

.locationDate > span:nth-child(4) {
  font-size: 3vh;
  width: 100%;
  text-align: center;
}

.locationDate img {
  height: 45vh;
  width: 25vw;
  align-self: flex-end;
}

@media (max-width: 641px) {
  .locationDate {
    display: flex;
    flex-direction: row;
    font-weight: bold;
    width: 100%;
    height: 38%;
    flex-wrap: wrap;
    justify-content: center;
  }

  .locationDate img {
    height: 30vh;
    width: 35vh;
  }

  .locationDate > span:nth-child(2) {
    font-size: 10vw;
    width: 15vw;
    height: 13vh;
  }

  .locationDate > span:nth-child(3) {
    display: none;
  }
}

